import options from './options';
import loading from '../utils/loading';
import { toast } from '../utils/toast';
// Evita aparecer a tela de senha novamente
var triedToAuthenticate = false;
// Comandos vindos do servidor
export var commands = {
    /** Exibe informações na tela */
    INFO: function (data) {
        toast(data);
    },
    /** Falha ao autenticar */
    AUTH_FAILED: function () {
        if (triedToAuthenticate)
            return;
        document.body.classList.remove('connecting', 'connected');
        document.body.classList.add('disconnected');
        var password = prompt('O computador requer uma senha para se conectar ao MobyStk');
        if (password === null)
            return;
        localStorage.setItem('joystick.password', password);
        triedToAuthenticate = true;
        loading();
        window.location.reload();
    }
};
/** Conexão do socket */
export var socket = socketConnect();
/** Tenta conectar ao socket */
export function socketConnect() {
    var ws;
    try {
        ws = new WebSocket('ws://' + options.ip);
    }
    catch (err) {
        console.error(err);
        closed();
        return;
    }
    ws.addEventListener('open', opened);
    ws.addEventListener('close', closed);
    ws.addEventListener('message', message);
    document.body.classList.remove('connected', 'disconnected');
    document.body.classList.add('connecting');
    /** Socket conectado */
    function opened() {
        document.body.classList.remove('connecting', 'disconnected');
        document.body.classList.add('connected');
        ws.send('PASSWORD ' + (options.password || ''));
    }
    /** Socket desconectado */
    function closed() {
        document.body.classList.remove('connecting', 'connected');
        document.body.classList.add('disconnected');
        setTimeout(function () { return (socket = socketConnect()); }, 3000);
    }
    /** Mensagem do socket */
    function message(e) {
        var _a = e.data.split(' '), cmd = _a[0], data = _a.slice(1);
        var command = commands[cmd];
        if (typeof command !== 'function')
            return;
        command(data.join(' '));
    }
    return ws;
}
